import Vue from 'vue'
import Router from 'vue-router'

//Generel main pages
import about from '@/components/about'
import work from '@/components/work'
import home from '@/components/home'

//Individual work, bio and resume pages.
import bio from '@/components/about/bio'
import resume from '@/components/about/resume'
import comeplaywithus from '@/components/work/comeplaywithus'
import compoodle from '@/components/work/compoodle'
import mufe from '@/components/work/mufe'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/',
      name: 'work',
      component: work
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/bio',
      name: 'bio',
      component: bio
    },
    {
      path: '/resume',
      name: 'resume',
      component: resume
    },
    {
      path: '/comeplaywithus',
      name: 'comeplaywithus',
      component: comeplaywithus
    },
    {
      path: '/compoodle',
      name: 'compoodle',
      component: compoodle
    },
    {
      path: '/mufe',
      name: 'mufe',
      component: mufe
    }
  ],
  mode: 'history'
})



