<template> 
	<div>
		<v-stepper class = "mt-4" v-model="e1">
        <v-stepper-header>
          <v-stepper-step step="1" :complete="e1 > 1">Trying to locate the problem</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="2" :complete="e1 > 2">Design process</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="3" :complete="e1 > 3">Production</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="4" :complete="e1 > 4">Showcase</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="5" :complete="e1 > 5">Evaluation</v-stepper-step>
        </v-stepper-header>


        <v-stepper-content step="1">
          <v-card class="grey lighten-1 z-depth-1 mb-5" height="200px"></v-card>
          <v-btn primary @click.native="e1 = 2">Continue</v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
        <v-stepper-content step="2">
          <v-card class="grey lighten-1 z-depth-1 mb-5" height="200px"></v-card>
          <v-btn primary @click.native="e1 = 3">Smutter</v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
        <v-stepper-content step="3">
          <v-card class="grey lighten-1 z-depth-1 mb-5" height="200px"></v-card>
          <v-btn primary @click.native="e1 = 4">Continue</v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
        <v-stepper-content step="4">
          <v-card class="grey lighten-1 z-depth-1 mb-5" height="200px"></v-card>
          <v-btn primary @click.native="e1 = 5">Continue</v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
        <v-stepper-content step="5">
          <v-card class="grey lighten-1 z-depth-1 mb-5" height="200px"></v-card>
          <v-btn primary @click.native="e1 = 1">Continue</v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
      </v-stepper>
	</div>
</template>

<script>
	export default {
	    data () {
	      return {
	        e1: 0     
	      }
    	}
  	}
</script>


